<script>
import Vue from 'vue'
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'
import { HImage } from '@handday/components'
Vue.use(Viewer)
export default {
  name: 'HanddayViewer',
  components: {
    HImage
  },
  render(h) {
    const props = { ...this.$attrs, ...this.$props}
    return h('viewer', {
      scopedSlots: {
        default: () => {
          return props.images.map((item, index) => {
            return h('h-image', {
              attrs: {
                src: item.preViewUrl,
                key: index,
                width: 100,
                height: 100
              }
            })
          })
        },
        ...this.$scopedSlots
      },
      props: {
        refs: 'v-viewer',
        ...props
      },
      on: {
        ...this.$listeners
      }
    })
  }
}
</script>
